import {
  Container,
  Grid,
  Button,
  TableContainer,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  TablePagination,
  Table,
  Chip,
} from "@material-ui/core";
import React, { memo, FC, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { NavLink, useHistory } from "react-router-dom";
import CustomAvatar from "../../../Component/CustomAvatar";
import DataTableSearch from "../../../Component/DataTableSearch";
import DataTableSort from "../../../Component/DataTableSort";
import IconButtonPopper from "../../../Component/IconButtonPopper/IconButtonPopper";
import LinearLoadingProgress from "../../../Component/LinearLoadingProgress";
import { InvalidDateTimeToDefault } from "../../../Hooks/UseDateParser";
import useFilter from "../../../Hooks/useFilter";
import FamilyActions from "../../../Services/Actions/FamilyActions";
import { setPageLinks } from "../../../Services/Actions/PageActions";
import ITableInitialSort from "../../../Services/Interface/ITableInitialSort";
import { FamMemberModel } from "../../../Services/Models/FamilyModel";
import { PaginationModel } from "../../../Services/Models/PaginationModels";
import { RootStore } from "../../../Services/Store";
import DialogViewFamMem from "./ManageFamilyAdminView/DialogViewFamMem";

interface IFamilyAdminView {}

const initialSearch = {
  search: "",
};

const initialTableSort: Array<ITableInitialSort> = [
  {
    label: "Newest first",
    value: {
      column: "encoded_at",
      direction: "desc",
    },
  },
  {
    label: "Oldest first",
    value: {
      column: "encoded_at",
      direction: "asc",
    },
  },
  {
    label: "Alphabetical Asc",
    value: {
      column: "ulo_fam_member",
      direction: "asc",
    },
  },
  {
    label: "Alphabetical Desc",
    value: {
      column: "ulo_fam_member",
      direction: "desc",
    },
  },
];

export const FamilyAdminView: FC<IFamilyAdminView> = memo(() => {
  const dispatch = useDispatch();
  const history = useHistory();

  const family_table = useSelector(
    (store: RootStore) => store.FamilyReducer.family_table
  );
  const fetch_family_table = useSelector(
    (store: RootStore) => store.FamilyReducer.fetch_family_table
  );

  const [selected_family_member, set_selected_family_member] =
    useState<null | Array<FamMemberModel>>(null);

  const [
    tableSearch,
    tableLimit,
    tablePage,
    tableCount,
    activeSort,
    searchField,
    selectedSortIndex,
    handleSetTableSearch,
    handleChangePage,
    handleChangeRowsPerPage,
    handleChagenSelectedSortIndex,
    handleSetSearchField,
  ] = useFilter(initialSearch, initialTableSort, 50);

  useEffect(() => {
    let mounted = true;
    const fetchTableData = () => {
      const filters: PaginationModel = {
        page: {
          begin: tablePage,
          limit: tableLimit,
        },
        sort: activeSort,
        filters: tableSearch,
      };

      dispatch(FamilyActions.getFamilyDataTable(filters));
    };

    mounted && activeSort && fetchTableData();

    return () => {
      mounted = false;
    };
  }, [activeSort, dispatch, tableLimit, tablePage, tableSearch]);

  useEffect(() => {
    dispatch(
      setPageLinks([
        {
          link: "/admin/family",
          title: "Families",
        },
      ])
    );
  }, [dispatch]);
  return (
    <>
      <Container maxWidth="lg">
        <Grid
          container
          spacing={3}
          style={{
            backgroundColor: `#fff`,
            padding: `1em`,
            borderRadius: 10,
          }}
        >
          <Grid item xs={12} container justify="flex-end" alignItems="center">
            <Grid item>
              <NavLink to="/admin/family/add">
                <Button disableElevation color="primary" variant="contained">
                  Create Family
                </Button>
              </NavLink>
            </Grid>
          </Grid>

          <Grid
            xs={12}
            item
            container
            spacing={1}
            alignItems="center"
            alignContent="center"
          >
            <Grid
              item
              xs={12}
              md={6}
              container
              spacing={2}
              justify="flex-start"
              alignContent="center"
              alignItems="center"
            >
              <Grid item>
                <TablePagination
                  rowsPerPageOptions={[50, 100, 500]}
                  component="div"
                  count={!!family_table?.count ? family_table.count : -1}
                  rowsPerPage={tableLimit}
                  page={tablePage}
                  onChangePage={handleChangePage}
                  onChangeRowsPerPage={handleChangeRowsPerPage}
                />
              </Grid>
            </Grid>
            <Grid
              item
              xs={12}
              md={6}
              container
              spacing={3}
              alignContent="center"
              alignItems="center"
              justify="flex-end"
            >
              <Grid item>
                <DataTableSort
                  handleChagenSelectedSortIndex={handleChagenSelectedSortIndex}
                  initialTableSort={initialTableSort}
                  selectedSortIndex={selectedSortIndex}
                />
              </Grid>

              <Grid item>
                <DataTableSearch
                  onSubmit={(e) => {
                    e.preventDefault();
                    handleSetTableSearch({
                      ...tableSearch,
                      search: searchField,
                    });
                  }}
                  handleSetSearchField={handleSetSearchField}
                  searchField={searchField}
                />
              </Grid>
            </Grid>
          </Grid>

          <Grid item xs={12}>
            <LinearLoadingProgress show={fetch_family_table} />
            <TableContainer>
              <Table size="small">
                <TableHead>
                  <TableRow>
                    <TableCell>Ulo sa Pamilya</TableCell>
                    <TableCell>Purok</TableCell>
                    <TableCell>Sakop sa Pamilya</TableCell>
                    {/* <TableCell>Kadugayon sa Pagpuyo</TableCell> */}
                    {/* <TableCell>Petsa</TableCell> */}
                    <TableCell>Aksyon</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {family_table?.table?.map((row, i) => (
                    <TableRow key={i}>
                      <TableCell>
                        <div className="table-cell-profile">
                          <CustomAvatar
                            className="image"
                            variant="circle"
                            height={4}
                            width={4}
                            src={`${row?.ulo_pamilya_info?.pic}`}
                            errorMessage={`${row?.ulo_pamilya_info?.first_name?.charAt(
                              0
                            )}${row?.ulo_pamilya_info?.first_name?.charAt(0)}`}
                          />
                          <NavLink
                            className="title"
                            to={`/admin/resident/${row?.ulo_pamilya_info.resident_pk}`}
                          >
                            <span style={{ textTransform: "capitalize" }}>
                              {row?.ulo_pamilya_info.last_name}
                              {", "}
                              {row?.ulo_pamilya_info.first_name}{" "}
                              {row?.ulo_pamilya_info.middle_name}{" "}
                              {row?.ulo_pamilya_info.suffix}
                            </span>
                          </NavLink>
                        </div>
                      </TableCell>

                      <TableCell>
                        Purok {row?.ulo_pamilya_info?.purok}
                      </TableCell>
                      <TableCell>
                        {/* */}

                        <Chip
                          onClick={() =>
                            set_selected_family_member(row.fam_members)
                          }
                          label={
                            <>
                              <b>{row?.fam_members.length}</b>{" "}
                              <small>kabuok miyembre</small>
                            </>
                          }
                          // label={`${row?.fam_members.length} kabuok sakop`}
                        />
                      </TableCell>
                      {/* <TableCell>
                        <small>
                          {InvalidDateTimeToDefault(row?.encoded_at, "-")}
                        </small>
                      </TableCell> */}
                      <TableCell align="center">
                        <IconButtonPopper
                          buttons={[
                            {
                              text: "Tan-awon ang tibuok impormasyon",
                              handleClick: () => {
                                history.push(`/admin/family/${row.fam_pk}`);
                              },
                            },
                          ]}
                        />
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Grid>
        </Grid>

        {selected_family_member && (
          <DialogViewFamMem
            family_members={selected_family_member}
            handleClose={() => set_selected_family_member(null)}
          />
        )}
      </Container>
    </>
  );
});

export default FamilyAdminView;
